import React from 'react'
import { Form, Button } from 'react-bootstrap';
import './form.css'

const ContactForm = () => {


    return (
        <div style={{width: '100%', padding: '12px'}}>
            <h3 style={{alignSelf: 'center'}}>Contact Me</h3>
            <Form action="https://formspree.io/xoqkklrn" method="POST" className="welcome-msg">
            <Form.Group controlId="formBasicName">
                <Form.Label>Name</Form.Label>
                <Form.Control type="email" placeholder="Enter your name" style={{width: '60%'}} />
            </Form.Group>

            <Form.Group controlId="formBasicEmail">
                <Form.Label>Email address</Form.Label>
                <Form.Control type="email" placeholder="Enter your email address" style={{width: '60%'}}/>
                <Form.Text className="text-muted" >
                I'll never share your email with anyone else.
                </Form.Text>
            </Form.Group>

            <Form.Group controlId="formBasicMessage">
                <Form.Label>Message</Form.Label>
                <Form.Control type="textarea" placeholder="Enter message" style={{width: '60%'}} />
            </Form.Group>
            <Button variant="primary" type="submit">
                Submit
            </Button>
        </Form>
        <br/>
        <div className="footer light"><i className="fa fa-copyright" aria-hidden="true"></i>Copyright Lwando Gqobho 2020</div>

        </div>
    )

}

export default ContactForm
