import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import './nav.css'
import { Navbar, Nav } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';

const Navba = () => {
    return (
        <div className="wrapper d-flex align-items-stretch"> 
          <Navbar style={{width: '100%', backgroundColor: '#145DA0'}} expand="lg" className="ml-auto">
                <Navbar.Brand style={{color:'white'}}>Lwando Gqobho: Web Developer</Navbar.Brand>
                <Nav.Link to="https://github.com/LwandoG" target="_blank" className="nav-item large icon4x"><i className="fab fa-lg fa-github"></i></Nav.Link>
                <Nav.Link to="https://www.linkedin.com/in/lwando-gqobho-369720158/" target="_blank" className="nav-item large icon4x"><i className="fab fa-linkedin-square" aria-hidden="true"></i></Nav.Link>
        </Navbar>
    </div>
    )
}

export default Navba
